const blobFromBase64String = base64String => {
  const byteArray = Uint8Array.from(
    atob(base64String)
      .split('')
      .map(char => char.charCodeAt(0))
  )
  return new Blob([byteArray], { type: 'image/jpg' })
}

const avatarB64 =
  ''

export default blobFromBase64String(avatarB64)
